<template>
  <swiper
    :modules="modules"
    :slides-per-view="1"
    :space-between="50"
    navigation
    :pagination="{ clickable: true ,el:'.swiper-pagination'}"
    :scrollbar="{ draggable: true }"
    @swiper="onSwiper"
    @slideChange="onSlideChange"
    class="swiper"
    v-show="status"
  >
    <swiper-slide v-for="(slider,index) in bannerSlider" :key="index"><img :src="slider.src" alt=""></swiper-slide>
  </swiper>
</template>
<script>
  // import Swiper core and required modules
  import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';

  // Import Swiper Vue.js components
  import { Swiper, SwiperSlide } from 'swiper/vue';

  // Import Swiper styles
  import 'swiper/swiper.min.css'
  import 'swiper/swiper-bundle.css'
  import { computed, onMounted, reactive, watch } from '@vue/runtime-core';
  import { getBannerSlider } from '@/const';
  import { useStore } from 'vuex';
  // import 'swiper/dist/navigation.min.css';
  // import 'pagination.min.css';
  // import 'scrollbar.min.css'

  // Import Swiper styles
  export default {
    components: {
      Swiper,
      SwiperSlide,
    },
    setup() {

      const store = useStore()
      const onSwiper = (swiper) => {

      };
      const onSlideChange = () => {
        console.log('slide change');
      };
      const bannerSlider = computed(()=>{
        
        return store.getters.doneBannerSlider
        
      })
      
      onMounted(()=>{
       
       store.dispatch(getBannerSlider)
     })
     const status = reactive({'swiper':false})
     watch(bannerSlider,()=>{
   
      if(typeof(store.state.home.bannerSlider.code)!='undefined'&&store.state.home.bannerSlider.code==200){status.swiper = true}})
      return {
        onSwiper,
        onSlideChange,
        bannerSlider,
        status,
        modules: [Navigation, Pagination, Scrollbar, A11y],
      };
    },
  };
</script>
<style scoped>
 img{
  width: 100%;
  height: 470px;
  object-fit: cover;
 }
</style>