<template>
  <div class="container" v-if="true">
        <div class="floorHd">
            <h3>为你推荐</h3>
        </div>
        <div class="floorTi">
            <ul class="feed-tab">
                <li>
                    <div class="feed-tab-tittle">
                        <span>精选</span>
                    </div>
                    <div class="feed-tab-desc">猜你喜欢</div>
                </li>
                <li>
                    <div class="feed-tab-tittle">
                        <span>智能先峰</span>
                    </div>
                    <div class="feed-tab-desc">大牌器城</div>
                </li>
                <li>
                    <div class="feed-tab-tittle">
                        <span>居家优品</span>
                    </div>
                    <div class="feed-tab-desc">品质生活</div>
                </li>
                <li>
                    <div class="feed-tab-tittle">
                        <span>超市百货</span>
                    </div>
                    <div class="feed-tab-desc"> 百货生鲜</div>
                </li>
            </ul>
        </div>
        <div class="container-goodsList">
            <ul class="list clearfix" ref="ul">
                <li class="list-item" v-for="(item) in goodsList" :key="item.pid">
                    <a href="baidu.com">
                    <div class="more">
                            <img :src="getSrc(item.img)" alt="">
                        <div>
                            <p>{{ item.t }}</p>
                        </div>
                        <div class="price">
                            <i>¥</i>
                            <span> {{ item.jp }}</span>
                        
                        </div>

                    </div>
                   </a>
                   
                </li>
            </ul>
        </div>

    </div>
  
</template>

<script>
import { computed, nextTick, onMounted, onUnmounted, onUpdated, watch } from '@vue/runtime-core'
import { useStore } from 'vuex'
import { reactive } from 'vue'
import { ref } from 'vue'
export default {
    setup(){
     
        const store = useStore()
        let ul = ref(null)
    
        onMounted(()=>{
            store.dispatch('getGoodsList',{'pages':status.pages})
            scroll()
           
            
        })

        const status = reactive({'pages':1,'show':false,'flag':true})
        const goodsList = computed(()=>{return store.state.home.goodsList})

       onUpdated(()=>{
        console.log('我已更新完毕')
        status.flag = true;
       })
       onUnmounted(()=>{
        window.removeEventListener('scroll',underScroll)
    

       })
        watch(store.state.home.goodsList,()=>{
            if(store.state.home.goodsList.length!==0){
            status.show = true
        }})
      function getSrc(src){
        const str = "https://img13.360buyimg.com/jdcms/s300x300_" + src
        return str

      }

    //   function underScroll(){
    //     console.log('111111');
    //     const triggle = (document.documentElement.scrollTop + window.screen.height - ul.value.offsetTop-ul.value.offsetHeight)>0
    //     const bottomOfWindow = document.documentElement.scrollHeight-document.documentElement.scrollTop - document.documentElement.clientHeight<0
    //     if(!bottomOfWindow){

    //             if(triggle&&status.flag==true){
    //                 status.flag = false
    //                 status.pages= status.pages + 1
    //                 store.dispatch('getGoodsList',{'pages':status.pages})
                
                    
    //             }
    //      }else{
    //         status.flag = false
    //  }

    //   }
      function debounce(){
        var timer = null
        
        return ()=>{
            clearTimeout(timer)
            timer = setTimeout(() => {
                const triggle = (document.documentElement.scrollTop + window.screen.height - ul.value.offsetTop-ul.value.offsetHeight)>0
                const bottomOfWindow = document.documentElement.scrollHeight-document.documentElement.scrollTop - document.documentElement.clientHeight<0
                if(!bottomOfWindow){
                    if(triggle&&status.flag==true){
                        status.flag = false
                        status.pages= status.pages + 1
                        store.dispatch('getGoodsList',{'pages':status.pages}) 
                    }
                }else{
                    status.flag = false
                }        
            },1000)
          }
      }

      let underScroll = debounce()

      function scroll(){
      
        let isLoading = true
        window.addEventListener('scroll',underScroll)   
        }
        return {
            status,
            goodsList,
            getSrc,
            scroll,
            
            ul
        }
    }

}
</script>

<style scoped>
  .container{
            width:1200px ; 
            margin: 0 auto;
            background-color: aliceblue;
   
        }
        .container>.floorHd{
            height: 60px;

        }
        .container > .floorHd > h3{
            width: 150px;
            font-size: 28px;
            margin: 0 auto;
        }
        .container .floorTi{

            background-color: white;
        }
        .container .floorTi .feed-tab{
            width: 1080px;
            list-style: none;
            padding: 0;
            display: flex;
            justify-content: center;
            cursor: pointer;
            
        }
        .container .floorTi .feed-tab li{
            display: inline-block;
            margin-left: 40px;
        }
        .container .container-goodsList{
            background-color: #f4f4f4;
        }
        .container .container-goodsList ul {
            width: 1200px;
            padding: 0;
            overflow: hidden;
        }
        .container .container-goodsList ul li{
            float: left;
            margin: 5px;
            width: 230px;
            height: 322px;
            background-color:white;
            list-style: none;
      
        }
        .container .container-goodsList ul li .more{
            position: relative;
        }
        .container .container-goodsList ul li img{
       
            display: block;
            margin: 30px auto 40px;
            width: 150px;
            height: 150px;
        }
        .container .container-goodsList ul li .more div {

            height: 48px;
            padding: 0 20px;
            overflow: hidden;
            text-overflow: ellipsis;
           
        }
        .container .container-goodsList ul li .more div p{
            width:190px;
            font-size: 14px;
            line-height: 24px;
            overflow: hidden;
            word-break: break-all;
          
            /* white-space: nowrap; */
            text-overflow: ellipsis;
           
          
            
        }
        .container .container-goodsList ul li a{
            width: 100%;
            height: 100%;
            display: inline-block;
            cursor: pointer;
        }
        .container .container-goodsList ul li .more .price i{
            color: red;
        }
        .container .container-goodsList ul li .more .price span{
            font-size: 20px;
            color:#e1251b;

        }
  

</style>>

