<template>
    <div class="today-recommend">
            <div class="py-container">
              <div class="seckiling clearfix">
                <a class="seckill-countdown" href="//miaosha.jd.com" target="_blank" clstag="h|keycount|core|seckill_a">
                    <div class="countdown-title">心享秒杀</div>
                    <div>
                      <div class="countdown-desc">
                        <a href="" class="iconfont icon-shandian"></a>
                        <strong>20:00</strong>点场 距结束
                      </div>
                      <span class="timmer countdown-main">
                        <span class="timmer__unit timmer__unit--hour">{{ status.hours }}</span>:
                        <span class="timmer__unit timmer__unit--minute">{{ status.minutes }}</span>:
                        <span class="timmer__unit timmer__unit--second">{{ status.seconds }}</span>
                      </span>
                    </div>
                </a>

              </div>
              <swiper
                :modules="modules"
                :slides-per-view="4"
                :space-between="50"
                navigation
                @swiper="onSwiper"
                @slideChange="onSlideChange"
                :slidesPerGroup="4"
                class="swiper"
                v-show="status.flashSale"
              >
              <swiper-slide v-for="(slider,index) in flashSale" :key="index"><img :src="slider.src" alt=""></swiper-slide>

             </swiper>
            </div>
  
        </div>
</template>

<script>
  import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';

  
  import { Swiper, SwiperSlide } from 'swiper/vue';


  import 'swiper/swiper.min.css'
  import 'swiper/swiper-bundle.css'
  import { computed, onMounted, reactive, watch } from '@vue/runtime-core';
  import { useStore } from 'vuex';
  import './index.css'

  export default {
    components: {
      Swiper,
      SwiperSlide,
    },
    setup() {
 
      const store = useStore()
      const status = reactive({'flashSale':false,})
      const flashSale = computed(()=>{return store.getters.doneFlashSale})
      const onSwiper = (swiper) => {

      };
      const onSlideChange = () => {
        console.log('slide change');
      };

      onMounted(()=>{
       store.dispatch('getFlashSale')
  
     })
     watch(flashSale,()=>{
       if(typeof(store.state.home.flashSale.code)!='undefined'&&store.state.home.flashSale.code==200){
               status.flashSale = true
       }
     })
    
    

     const time = new Date()
     const focusTime= time.getTime() + 3600000

       
        
    function miaosha(time){
        const curTime = Date.now()
        const timeSpan = time - curTime
        
        status.hours = Math.floor(timeSpan/3600000)<=9? '0' + Math.floor(timeSpan/3600000):Math.floor(timeSpan/3600000)

      
        status.minutes = Math.floor( (timeSpan - status.hours*360000)/60000)<=9? '0'+Math.floor( (timeSpan - status.hours*360000)/60000):Math.floor( (timeSpan - status.hours*360000)/60000)
        status.seconds = Math.floor((timeSpan - status.hours*360000- status.minutes *60000)/1000)<=9? '0'+Math.floor((timeSpan - status.hours*360000- status.minutes *60000)/1000):Math.floor((timeSpan - status.hours*360000- status.minutes *60000)/1000)
     
    }
   setInterval(miaosha,1000,focusTime)
      return {
        onSwiper,
        onSlideChange,
        status,
        flashSale,
        modules: [Navigation, Pagination, Scrollbar, A11y],
      };
    },

}
</script>

<style>
@import url('./index.css');
@import url('https://at.alicdn.com/t/c/font_3840995_v4eh34hrxe.css');
 .today-recommend .py-container {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  
}
.today-recommend .py-container .seckiling{
  width: 200px;
  height: 250px;

 


}
.today-recommend .py-container .swiper{
  
  flex: 5;

}
</style>